    /**
     * Checks whether the x and y coordinates given are contained within this Rectangle
     *
     * @param x {number} The X coordinate of the point to test
     * @param y {number} The Y coordinate of the point to test
     * @return {boolean} Whether the x/y coordinates are within this Rectangle
     */
    Rectangle.prototype.contains = function (x, y)
    {
        if (this.width <= 0 || this.height <= 0)
        {
            return false;
        }
    
        if (x >= this.x && x < this.x + this.width)
        {
            if (y >= this.y && y < this.y + this.height)
            {
                return true;
            }
        }
    
        return false;
    };
    
    /**
     * 矩阵对象，用来描述和记录对象的tansform 状态信息
     *
     * @class
     * @memberof JC
     */
    function Matrix() {
        this.a = 1;
        this.b = 0;
        this.c = 0;
        this.d = 1;
        this.tx = 0;
        this.ty = 0;
    }
    JC.Matrix = Matrix;
    Matrix.prototype.constructor = JC.Matrix;
    
    /**
     * 从数组设置一个矩阵
     *
     * @param array {number[]}
     */
    Matrix.prototype.fromArray = function(array) {
        this.a = array[0];
        this.b = array[1];
        this.c = array[3];
        this.d = array[4];
        this.tx = array[2];
        this.ty = array[5];
    };
    
    /**
     * 将对象的数据以数组的形式导出
     *
     * @param transpose {boolean} 是否对矩阵进行转置
     * @return {number[]} 返回数组
     */
    Matrix.prototype.toArray = function(transpose) {
        if (!this.array) this.array = new Float32Array(9);
        var array = this.array;
    
        if (transpose) {
            array[0] = this.a;
            array[1] = this.b;
            array[2] = 0;
            array[3] = this.c;
            array[4] = this.d;
            array[5] = 0;
            array[6] = this.tx;
            array[7] = this.ty;
            array[8] = 1;
        } else {
            array[0] = this.a;
            array[1] = this.c;
            array[2] = this.tx;
            array[3] = this.b;
            array[4] = this.d;
            array[5] = this.ty;
            array[6] = 0;
            array[7] = 0;
            array[8] = 1;
        }
        return array;
    };
    
    /**
     * 将坐标点与矩阵左乘
     *
     * @param pos {object} 原始点
     * @param newPos {object} 变换之后的点
     * @return {object} 返回数组
     */
    Matrix.prototype.apply = function(pos, newPos) {
        newPos = newPos || {};
        newPos.x = this.a * pos.x + this.c * pos.y + this.tx;
        newPos.y = this.b * pos.x + this.d * pos.y + this.ty;
        return newPos;
    };
    /**
     * 将坐标点与转置矩阵左乘
     *
     * @param pos {object} 原始点
     * @param newPos {object} 变换之后的点
     * @return {object} 变换之后的点
     */
    Matrix.prototype.applyInverse = function(pos, newPos) {
        var id = 1 / (this.a * this.d + this.c * -this.b);
        newPos.x = this.d * id * pos.x + -this.c * id * pos.y + (this.ty * this.c - this.tx * this.d) * id;
        newPos.y = this.a * id * pos.y + -this.b * id * pos.x + (-this.ty * this.a + this.tx * this.b) * id;
        return newPos;
    };
    /**
     * 位移操作
     *
     * @return {this} 
     */
    Matrix.prototype.translate = function(x, y) {
        this.tx += x;
        this.ty += y;
        return this;
    };
    /**
     * 缩放操作
     *
     * @return {this} 
     */
    Matrix.prototype.scale = function(x, y) {
        this.a *= x;
        this.d *= y;
        this.c *= x;
        this.b *= y;
        this.tx *= x;
        this.ty *= y;
        return this;
    };
    /**
     * 旋转操作
     *
     * @return {this} 
     */
    Matrix.prototype.rotate = function(angle) {
        var cos = Math.cos(angle);
        var sin = Math.sin(angle);
        var a1 = this.a;
        var c1 = this.c;
        var tx1 = this.tx;
        this.a = a1 * cos - this.b * sin;
        this.b = a1 * sin + this.b * cos;
        this.c = c1 * cos - this.d * sin;
        this.d = c1 * sin + this.d * cos;
        this.tx = tx1 * cos - this.ty * sin;
        this.ty = tx1 * sin + this.ty * cos;
        return this;
    };
    /**
     * 矩阵相乘
     *
     * @return {this} 
     */
    Matrix.prototype.append = function(matrix) {
        var a1 = this.a;
        var b1 = this.b;
        var c1 = this.c;
        var d1 = this.d;
        this.a = matrix.a * a1 + matrix.b * c1;
        this.b = matrix.a * b1 + matrix.b * d1;
        this.c = matrix.c * a1 + matrix.d * c1;
        this.d = matrix.c * b1 + matrix.d * d1;
        this.tx = matrix.tx * a1 + matrix.ty * c1 + this.tx;
        this.ty = matrix.tx * b1 + matrix.ty * d1 + this.ty;
        return this;
    };
    /**
     * 单位矩阵
     *
     * @return {this} 
     */
    Matrix.prototype.identity = function() {
        this.a = 1;
        this.b = 0;
        this.c = 0;
        this.d = 1;
        this.tx = 0;
        this.ty = 0;
        return this;
    };
    /**
     * 快速设置矩阵各个分量
     *
     * @return {this} 
     */
    Matrix.prototype.setTransform = function(x, y, pivotX, pivotY, scaleX, scaleY, rotation, skewX, skewY) {
        var a, b, c, d, sr, cr, cy, sy, nsx, cx;
    
        sr = Math.sin(rotation);
        cr = Math.cos(rotation);
        cy = Math.cos(skewY);
        sy = Math.sin(skewY);
        nsx = -Math.sin(skewX);
        cx = Math.cos(skewX);
    
        a = cr * scaleX;
        b = sr * scaleX;
        c = -sr * scaleY;
        d = cr * scaleY;
    
        this.a = cy * a + sy * c;
        this.b = cy * b + sy * d;
        this.c = nsx * a + cx * c;
        this.d = nsx * b + cx * d;
    
        this.tx = x + (pivotX * a + pivotY * c);
        this.ty = y + (pivotX * b + pivotY * d);
    
        return this;
    };
    JC.IDENTITY = new Matrix();
    JC.TEMP_MATRIX = new Matrix();
    

    function BaseTexture(source, scaleMode) {
        JC.Eventer.call(this);
    
        this.width = 100;
    
        this.height = 100;
    
        this.realWidth = 100;
    
        this.realHeight = 100;
    
        this.scaleMode = scaleMode || JC.CONST.SCALE_MODES.DEFAULT;
    
        this.hasLoaded = false;
    
        this.isLoading = false;
    
        this.source = null; // set in loadSource, if at all
    
        this.resolution = 1;
    
        this.premultipliedAlpha = true;
    
        this.imageUrl = null;
    
        this.isPowerOfTwo = false;
    
        this.mipmap = false;
    
        // this._glTextures = {};
    
        // if no source passed don't try to load
        if (source) {
            this.loadSource(source);
        }
    
    }
    
    BaseTexture.prototype = Object.create(JC.Eventer.prototype);
    BaseTexture.prototype.constructor = BaseTexture;
    
    BaseTexture.prototype.update = function() {
        this.realWidth = this.source.naturalWidth || this.source.width;
        this.realHeight = this.source.naturalHeight || this.source.height;
    
        this.width = this.realWidth / this.resolution;
        this.height = this.realHeight / this.resolution;
    
        this.isPowerOfTwo = JC.UTILS.isPowerOfTwo(this.realWidth, this.realHeight);
    
        this.emit({ type: 'update' });
    };
    
    BaseTexture.prototype.loadSource = function(source) {
        var wasLoading = this.isLoading;
        this.hasLoaded = false;
        this.isLoading = false;
    
        this.source = source;
    
        if ((this.source.complete || this.source.getContext) && this.source.width && this.source.height) {
            this._sourceLoaded();
        } else if (!source.getContext) {
            this.isLoading = true;
    
            var This = this;
    
            source.onload = function() {
                source.onload = null;
                source.onerror = null;
    
                This.isLoading = false;
                This._sourceLoaded();
                This.emit({ type: 'loaded' });
            };
    
            source.onerror = function() {
                source.onload = null;
                source.onerror = null;
    
                This.isLoading = false;
                This.emit({ type: 'error' });
            };
    
            if (source.complete && source.src) {
                this.isLoading = false;
    
                source.onload = null;
                source.onerror = null;
    
                if (source.width && source.height) {
                    this._sourceLoaded();
    
                    // If any previous subscribers possible
                    if (wasLoading) {
                        this.emit({ type: 'loaded' });
                    }
                } else {
                    // If any previous subscribers possible
                    if (wasLoading) {
                        this.emit({ type: 'error' });
                    }
                }
            }
        }
    };
    
    BaseTexture.prototype._sourceLoaded = function() {
        this.hasLoaded = true;
        this.update();
    };
    BaseTexture.fromImage = function(imageUrl, crossorigin, scaleMode) {
        var baseTexture = JC.UTILS.BaseTextureCache[imageUrl];
    
        if (crossorigin === undefined && imageUrl.indexOf('data:') !== 0) {
            crossorigin = true;
        }
    
        if (!baseTexture) {
            var image = new Image();
            if (crossorigin) {
                image.crossOrigin = '';
            }
    
            baseTexture = new BaseTexture(image, scaleMode);
            baseTexture.imageUrl = imageUrl;
    
            image.src = imageUrl;
    
            JC.UTILS.BaseTextureCache[imageUrl] = baseTexture;
    
        }
    
        return baseTexture;
    };
    
    function Texture(baseTexture, frame) {
        JC.Eventer.call(this);
    
        this.noFrame = false;
    
        if (!frame) {
            this.noFrame = true;
            frame = new JC.Rectangle(0, 0, 1, 1);
        }
    
        if (baseTexture instanceof Texture) {
            baseTexture = baseTexture.baseTexture;
        }
    
        this.baseTexture = baseTexture;
    
        this._frame = frame;
    
        this.valid = false;
    
        this.requiresUpdate = false;
    
        this._uvs = null;
    
        this.width = 0;
    
        this.height = 0;
    
        if (baseTexture.hasLoaded) {
            if (this.noFrame) {
                frame = new JC.Rectangle(0, 0, baseTexture.width, baseTexture.height);
    
                // if there is no frame we should monitor for any base texture changes..
                baseTexture.on('update', this.onBaseTextureUpdated.bind(this));
            }
            this.frame = frame;
        } else {
            baseTexture.once('loaded', this.onBaseTextureLoaded.bind(this));
        }
    
    }
    JC.Texture = Texture;
    Texture.prototype = Object.create(JC.Eventer.prototype);
    Texture.prototype.constructor = Texture;
    
    Object.defineProperties(Texture.prototype, {
        frame: {
            get: function() {
                return this._frame;
            },
            set: function(frame) {
                this._frame = frame;
    
                this.noFrame = false;
    
                this.width = frame.width;
                this.height = frame.height;
    
                this.valid = frame && frame.width && frame.height && this.baseTexture.hasLoaded;
                if (this.valid) {
                    this._updateUvs();
                }
            }
        }
    });
    
    Texture.prototype.update = function() {
        this.baseTexture.update();
    };
    
    Texture.prototype.onBaseTextureLoaded = function() {
        // TODO this code looks confusing.. boo to abusing getters and setterss!
        if (this.noFrame) {
            this.frame = new JC.Rectangle(0, 0, this.baseTexture.width, this.baseTexture.height);
        } else {
            this.frame = this._frame;
        }
    
        this.emit({type: 'loaded'});
    };
    
    Texture.prototype.onBaseTextureUpdated = function() {
        this._frame.width = this.baseTexture.width;
        this._frame.height = this.baseTexture.height;
    
        this.emit({type: 'update'});
    };
    
    Texture.prototype._updateUvs = function() {
        if (!this._uvs) {
            this._uvs = new TextureUvs();
        }
        this._uvs.set(this.frame, this.baseTexture);
    };
    
    Texture.fromImage = function(imageUrl, crossorigin, scaleMode) {
        var texture = JC.UTILS.TextureCache[imageUrl];
    
        if (!texture) {
            texture = new Texture(BaseTexture.fromImage(imageUrl, crossorigin, scaleMode));
            JC.UTILS.TextureCache[imageUrl] = texture;
        }
    
        return texture;
    };
    
    Texture.fromCanvas = function(canvas, scaleMode) {
        return new Texture(BaseTexture.fromCanvas(canvas, scaleMode));
    };
    
    function TextureUvs() {
        this.x0 = 0;
        this.y0 = 0;
    
        this.x1 = 1;
        this.y1 = 0;
    
        this.x2 = 1;
        this.y2 = 1;
    
        this.x3 = 0;
        this.y3 = 1;
    }
    
    TextureUvs.prototype.set = function(frame, baseFrame) {
        var tw = baseFrame.width;
        var th = baseFrame.height;
    
        this.x0 = frame.x / tw;
        this.y0 = frame.y / th;
    
        this.x1 = (frame.x + frame.width) / tw;
        this.y1 = frame.y / th;
    
        this.x2 = (frame.x + frame.width) / tw;
        this.y2 = (frame.y + frame.height) / th;
    
        this.x3 = frame.x / tw;
        this.y3 = (frame.y + frame.height) / th;
    };
    TextureUvs.prototype.get = function() {
        return new Float32Array([
            this.x0, this.y0, this.x1, this.y1,
            this.x2, this.y2, this.x3, this.y3
        ]);
    };
    

