    /**
     * 获取当前坐标系下的监测区域
     *
     * @method getBounds
     * @private
     */
    DisplayObject.prototype.getBounds = function() {
        var bounds = [],
            l = this.bounds.length >> 1;
    
        for (var i = 0; i < l; i++) {
            var p = this.worldTransform.apply({ x: this.bounds[i * 2], y: this.bounds[i * 2 + 1] });
            bounds[i * 2] = p.x;
            bounds[i * 2 + 1] = p.y;
        }
        return bounds;
    };
