            this.dynamicData[i*this.dynamicStride+1] = this.vertices[i*2+1];
    
            this.dynamicData[i*this.dynamicStride+2] = uvs[i*2];
            this.dynamicData[i*this.dynamicStride+3] = uvs[i*2+1];
    
    
